<template>
    <div>
        <van-row type="flex" align="center">
            <van-col span="12"><input type="text"></van-col>
            <van-col span="12">
                <van-button type="primary" class="mlr10">搜索</van-button>
                <van-button type="danger">新增</van-button>
            </van-col>
        </van-row>
        <table>
            <tr>
                <th width="15%">id</th>
                <th width="25%">用户名</th>
                <th width="25%">性别</th>
                <th>操作</th>
            </tr>
            <tr v-for="(x,index) in myData" :key="x.id">
                <td>{{x.id}}</td>
                <td>{{x.name}}</td>
                <td>{{x.sex}}</td>
                <td><span @click="editFn(index)">编辑</span><span @click="deleteFn(index)">删除</span></td>
            </tr>
            <tr>
                <td colspan="4" v-show="myData.length===0">暂无用户</td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        name: "TableOne",
        data: function () {
            let myData = [{id: 1, name: "张三", sex: "男"}, {id: 2, name: "李四", sex: "女"}];
            return {
                myData
            }
        },
        methods: {
            deleteFn: function (index) {
                this.$dialog.confirm({
                    title: '操作提示',
                    message: '删除这个用户吗？'
                }).then(() => {
                    this.myData.splice(index, 1);
                }).catch(() => {
                    // on cancel
                });
            }
        }
    }
</script>

<style scoped lang="scss">
    input {
        width:100%;
        box-sizing:border-box;
    }
    table {
        margin-top:20px;
        width:100%;
        border-collapse:collapse;
        font-size:14px;
        th {
            background-color:#eee;
        }
        th, td {
            text-align:center;
            padding:5px;
            border:1px solid #ccc;
            span:first-child {
                margin-right:10px;
            }
        }
    }
</style>
